@use 'sass:math';

html,
body {
    box-sizing: border-box;
    height: 100%;
}


// REM
@function rem($px) {
    $result: math.div($px, 16) + rem;
    @return $result;
}



ul {
    list-style: none;
}

img {
    max-width: 100%;
    display: block;
}

input,
button,
textarea,
select {
    font: inherit;
}

a {
    display: block;
    text-decoration: none;
    color: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
    padding: 0;
    // font-size: inherit;
}

address {
    font-style: normal;
}

body {
    font-family: 'Roboto', sans-serif;
    // @extend %second-family-400;
    font-weight: 400;
    font-size: 14px;
    // font-size: clamp(0.75rem, 0.556rem + 0.86vw, 1.25rem);
    // line-height: 1.6;
    color: $text;
    overflow-x: hidden;
    min-width: 100vw;
}

button {
    padding: 0;
    margin: 0;
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    background-color: transparent;
    cursor: pointer;
    border: none;
}

::marker {
    margin-top: 10px;
}


