.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

.overlay {
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(#000, .3);
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(6px);
    z-index: 9;
    opacity: 0;
    visibility: hidden;
    @include transition ($transition-property: opacity);

    &--active {
        opacity: 1;
        visibility: visible;
    }
}

.dis-scroll {
    position: relative;
    overflow: hidden;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
}

.overflow {
    overflow-x: auto;
    overflow-y: hidden;
}

.flex {
    display: flex;
}

.aic {
    align-items: center;
}

.jcbs {
    justify-content: space-between;
}